import * as React from 'react';
import { TBItemSlot } from '@ali/we-design-next';
import { ColorIconMenu } from './components/next/colorIconMenu';
import { BiPlugin } from '../../types';


export default function createRenderHoverToolbar(
  config: any
): BiPlugin['renderHoverToolbar'] {
  return (props, controller, getNode) => {
    const { buttonName, extraProps } = props;
    switch (buttonName) {
      case 'style':
        return getNode() ? (
          <TBItemSlot display='iconButton'>
            {
               (
                <ColorIconMenu
                  controller={controller}
                  getNode={getNode}
                  tooltip={config.locale?.colorTitle}
                  {
                    ...extraProps
                  }
                />
              ) 
            }
          </TBItemSlot>
        ) : null;
      default: return null;
    }
  };
}
